._HwoTi {
  width: 100%;
  height: 100%;
  min-height: 100%;
  position: relative;
  overflow: hidden;
}

._HwoTi * {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

._2c9oQ {
  width: 100%;
  height: 100%;
  position: absolute;
  overflow: visible;
  transform-origin: 0px 0px;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
}

._HwoTi:active ._2c9oQ {
  will-change: transform;
}
